import React from "react";
import classes from "./Contact.module.css";
import { IconContext } from "react-icons";
import { FaFacebookSquare, FaInstagram } from "react-icons/fa";

const Contact = () => {
  return (
    <React.Fragment>
      <div className={classes.center}>
        <div className={classes.container}>
          <h2>Come Say Hi!</h2>
          <div className={classes.Box}>
            <p>pawsnclaws@gmail.com</p>
            <p>6655583877</p>
          </div>
          <p className={classes.Shout}>Follow Us On...</p>
          <div className={classes.socialLinks}>
            <a
              href="https://www.facebook.com/Paws-N-Claws-105140635002371/"
              target="_blank"
              rel="noreferrer"
            >
              <IconContext.Provider
                value={{ size: "50px", className: classes.socialLink }}
              >
                <FaFacebookSquare />
              </IconContext.Provider>
            </a>
            <a
              href="https://www.instagram.com/paws_n_claws10/"
              target="_blank"
              rel="noreferrer"
            >
              <IconContext.Provider
                value={{ size: "50px", className: classes.socialLink }}
              >
                <FaInstagram />
              </IconContext.Provider>
            </a>
          </div>
          <p></p>
        </div>
      </div>
    </React.Fragment>
  );
};

export default Contact;
