import React from "react";
import classes from "./About.module.css";
import cats from "../../assets/cats.jpg";
import dogs from "../../assets/dogs2.jpg";

const About = () => {
  return (
    <React.Fragment>
      <div className={classes.center}>
        <div className={classes.container}>
          <p>About Us</p>
          <div className={classes.Hero}>
            <img src={cats} alt="Cats" className={classes.Img} />
            <p>
              We are an easily find local and trusted pet sitters from Bengaluru
              to pick in and drop visits. This means a pet sitter will come and
              visit your pet at a time you both agree on and provide lots of
              love and personal attention. These drop in visits are the least
              stressful option for your pet because your pet will get to stay in
              the best environment – their own home in Bengaluru. Their
              surroundings will be familiar to them and our loving and
              passionate pet sitter. Our sitters will ensure they treat your
              pets with respect and are flexible with timings, requests and
              durations. All this can be discussed before you make a booking. So
              whether you need just one day, a whole week or even a month,
              you’ll be able to find an experienced pet carer to accommodate
              your pet’s needs in Bengaluru.
            </p>
          </div>
          <div className={classes.Hero}>
            <p>
              Our pet carers also have a range of experiences which include
              puppies, seniors, reserved pets and dog training. So if your pet
              has any special requirements, you’ll be sure to find a pet sitter
              that is a good match for you. Your pet’s safety and welfare is a
              big priority for us and that is why all of our pet sitters go
              through a strict vetting process by our trust and safety team.
              Each pet sitting booking made through Paws N Claws is also covered
              by our premium insurance and 24-hour customer support team for pet
              parents and pet sitters. You can rest easy knowing that your pets
              will be well looked after by one of our expert pet carers in
              Bengaluru when you are not home. Pet owners book Pet Boarding in
              Bengaluru on Paws N Claws.
            </p>
            <img src={dogs} alt="Cats" className={classes.Img} />
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};

export default About;
